/* src/app/features/courses/courses-page.component.css */
.courses-container {
  padding: 3rem 1rem;
}
.course-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.75rem;
  margin-top: 2.25rem;
}
.course-card {
  position: relative;
  background:
    linear-gradient(
      145deg,
      rgba(26, 35, 50, 1) 0%,
      rgba(15, 23, 36, 1) 100%);
  padding: 1.75rem 1.5rem 2.25rem;
  border-radius: 16px;
  color: #fff;
  border: 1px solid var(--border);
  box-shadow: 0 10px 28px -6px rgba(0, 0, 0, .45);
  overflow: hidden;
  transition:
    transform .35s ease,
    box-shadow .35s ease,
    border-color .35s;
}
.course-card:before {
  content: "";
  position: absolute;
  inset: 0;
  background:
    radial-gradient(
      circle at 80% 15%,
      rgba(0, 194, 138, 0.15),
      transparent 60%);
  opacity: .6;
}
.course-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 18px 40px -8px rgba(0, 0, 0, .55);
  border-color: var(--brand-accent);
}
.course-card h3 {
  font-size: 1.3rem;
  font-weight: 600;
  margin: .75rem 0 1rem;
}
.course-card p {
  font-size: .95rem;
  line-height: 1.55;
  color: var(--text-muted);
  margin: 0 0 1.1rem;
}
.course-card .meta {
  display: flex;
  gap: 1rem;
  font-size: .7rem;
  text-transform: uppercase;
  letter-spacing: .5px;
  color: var(--text-secondary);
}
.course-card .action {
  margin-top: 1.25rem;
}
.course-card .action button {
  width: 100%;
  background:
    linear-gradient(
      90deg,
      #0B6EFF,
      #00C28A);
  border: none;
  color: #fff;
  padding: .65rem 1rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: filter .25s;
}
.course-card .action button:hover {
  filter: brightness(1.1);
}
